"use client";

import { Modal, Form, Button, Row, Col } from "react-bootstrap";
import { useState } from "react";

interface Props {
  show: boolean;
  onHide: () => void;
}
const AddPantryModal: React.FC<Props> = ({ show, onHide }) => {
  return (
    <Modal
      show={show}
      onHide={onHide}
      backdrop="static"
      keyboard={false}
      centered
      contentClassName="custom-modal"
    >
      <Modal.Header
        style={{ borderBottom: "none", paddingBottom: "0px" }}
        closeButton
      />
      <Modal.Body className="text-center">
        <h4>Add New Pantry</h4>
        <Form /*onSubmit={handleSubmit}*/>
          <Form.Group className="mb-3" controlId="storageName">
            <Form.Control
              className="text-center"
              autoComplete="off"
              placeholder="Storage Name"
              /* value={formData.name} */
              /* onChange={(e) => handleChange("name", e.target.value)} */
              required
            />
          </Form.Group>

          <Row className="mb-3">
            <Col>
              <Form.Group controlId="storageType">
                <Form.Control
                  className="text-center"
                  autoComplete="off"
                  placeholder="Storage Type"
                  disabled
                  /* value={formData.quantity} */
                  /* onChange={(e) => handleChange("quantity", e.target.value)} */
                  required
                />
              </Form.Group>
            </Col>
          </Row>

          <Button variant="success" type="submit">
            Add Item
          </Button>
        </Form>
      </Modal.Body>
    </Modal>
  );
};

export default AddPantryModal;
